import uniIcon from "@/components/uni-icons/uni-icons.vue";
export default {
	components:{
		uniIcon
	},
	props:{
		title:{
			type: String,
			default: '提示'
		}
	},
	data() {
		return {
			items: [{
					id: 'USA',
					label: '价格太高'
				},
				{
					id: 'CHN',
					label: '布面不干净'
					
				},
				{
					id: 'BRA',
					label: '买家不满意'
				}],
			current: 0,
			bannerShow:false,
			content: ''
		};
	},
	methods:{
		radioChange:function(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].id === evt.target.id) {
					this.current = i;
					this.content = this.items[i].label
					break;
				}
			}
			
		},
		closeBanner:function(){
			
			this.bannerShow=false;
		},
		upShop:function(){
			
			uni.showToast({
				title: '待开发',
				icon: 'none',
			});
			return
		},
		show:function(){
			this.bannerShow=true;
		},
		confirmBanner:function(){
			this.$emit('input')
			this.bannerShow = false
		}
	}
	
}
